import React from 'react'
import { Box, Typography, Card, Stack, useMediaQuery } from "@mui/material";
import { useNavigate } from 'react-router-dom';
import DescriptionRoundedIcon from '@mui/icons-material/DescriptionRounded';
import FormatAlignLeftRoundedIcon from '@mui/icons-material/FormatAlignLeftRounded';
import ChatRoundedIcon from '@mui/icons-material/ChatRounded';
import ImageRoundedIcon from '@mui/icons-material/ImageRounded';

const Homepage = () => {
    const isNotMobile = useMediaQuery("(min-width:1000px)")
    const navigate = useNavigate();
    return (
        <>
            <Box sx={{ display: "flex", flexDirection: { sm: "row", xs: "column" }, gap: { sm: 4, md: 1.5, lg: 6, xl: 15 } }} >
                {/* summary */}
                <Box p={2}>
                    <Typography variant="h4" mb={2} fontWeight="bold">
                        Summary Generator
                    </Typography>
                    <Card onClick={() => navigate("/summary")} sx={{
                        boxShadow: 2, borderRadius: 5, height: 190, width: 200,
                        '&:hover': { border: 2, boxShadow: 0, borderColor: "primary.dark", cursor: "pointer" }
                    }}>
                        <DescriptionRoundedIcon sx={{ fontSize: 80, color: "primary.main", mt: 2, ml: 2 }} />
                        <Stack p={3} pt={0}>
                            <Typography fontWeight="bold" variant="h5">TEXT SUMMARY</Typography>
                            <Typography variant="h6">Summarize long text into short text</Typography>
                        </Stack>
                    </Card>
                </Box>
                {/* paragraph */}
                <Box p={2}>
                    <Typography variant="h4" mb={2} fontWeight="bold">
                        Paragraph Generator
                    </Typography>
                    <Card onClick={() => navigate("/paragraph")} sx={{
                        boxShadow: 2, borderRadius: 5, height: 190, width: 200,
                        '&:hover': { border: 2, boxShadow: 0, borderColor: "primary.dark", cursor: "pointer" }
                    }}>
                        <FormatAlignLeftRoundedIcon sx={{ fontSize: 80, color: "primary.main", mt: 2, ml: 2 }} />
                        <Stack p={3} pt={0}>
                            <Typography fontWeight="bold" variant="h5">PARAGRAPH</Typography>
                            <Typography variant="h6">Generate a short paragraph on a topic</Typography>
                        </Stack>
                    </Card>
                </Box>
                {/* Ai chat bot */}
                <Box p={2}>
                    <Typography variant="h4" mb={2} fontWeight="bold" ml={2}>
                        Chat Bot
                    </Typography>
                    <Card onClick={() => navigate("/chatbot")} sx={{
                        boxShadow: 2, borderRadius: 5, height: 190, width: 200,
                        '&:hover': { border: 2, boxShadow: 0, borderColor: "primary.dark", cursor: "pointer" }
                    }}>
                        <ChatRoundedIcon sx={{ fontSize: 80, color: "primary.main", mt: 2, ml: 2 }} />
                        <Stack p={3} pt={0}>
                            <Typography fontWeight="bold" variant="h5" >CHAT BOT</Typography>
                            <Typography variant="h6">Have a chat with chat bot</Typography>
                        </Stack>
                    </Card>
                </Box>
                {/* scifi-image */}
                <Box p={2}>
                    <Typography variant="h4" mb={2} fontWeight="bold" ml={2}>
                        SCIFI IMAGE
                    </Typography>
                    <Card onClick={() => navigate("/scifi-img")} sx={{
                        boxShadow: 2, borderRadius: 5, height: 190, width: 200,
                        '&:hover': { border: 2, boxShadow: 0, borderColor: "primary.dark", cursor: "pointer" }
                    }}>
                        <ImageRoundedIcon sx={{ fontSize: 80, color: "primary.main", mt: 2, ml: 2 }} />
                        <Stack p={3} pt={0}>
                            <Typography fontWeight="bold" variant="h5">SCIFI IMAGE</Typography>
                            <Typography variant="h6">Generate a scifi image</Typography>
                        </Stack>
                    </Card>
                </Box>
            </Box>
        </>
    )
}

export default Homepage