import React, { useState } from 'react';
import { Box, Typography, useTheme, useMediaQuery, TextField, Button, Alert, Collapse, Card } from "@mui/material";
import { Link, NavLink, useNavigate } from 'react-router-dom';
import toast from 'react-hot-toast';
import axios from "axios";

const Summary = () => {
    const theme = useTheme();
    const navigate = useNavigate();
    //mediaquery
    const isNotMobile = useMediaQuery("(min-width:1000px)")
    //states
    const [text, setText] = useState("");
    const [summary, setSummary] = useState("");
    const [error, setError] = useState("");

    const loggedIn = JSON.parse(localStorage.getItem("authToken"));

    const handleSubmit = async (e) => {
        e.preventDefault()
        try {
            const { data } = await axios.post("/api/v1/openai/summary", { text });
            setSummary(data);
        } catch (err) {
            console.log(err);
            if (err.response.data.error) {
                setError(err.message)
            }
            else if (err.message) {
                setError(err.message)
            }
            setTimeout(() => { setError("") }, 5000)
        }
    }
    return (
        <Box width={isNotMobile ? "40%" : "80%"} p={"2rem"} m={"2rem auto"}
            borderRadius={5}
            sx={{ boxShadow: 5 }}
            backgroundColor={theme.palette.background.alt}
        >
            <Collapse in={error}>
                <Alert severity="error" sx={{ mb: 2 }}>{error}</Alert>
            </Collapse>
            {loggedIn ? (<><form onSubmit={handleSubmit}>
                <Typography variant="h3">Summarize</Typography>

                <TextField placeholder="add your text" type="text" multiline={true} required margin="normal" fullWidth value={text}
                    onChange={(e) => { setText(e.target.value) }} />


                <Button type="submit" fullWidth variant="contained" size="large" sx={{ color: "white", mt: 2 }}>
                    Submit
                </Button>
                <Typography mt={2}>Not this tool?<Link to="/">Go Back</Link></Typography>
            </form>
                {summary ? (
                    <Card sx={{
                        mt: 4, border: 1, boxShadow: 0, height: "500px", borderRadius: 5,
                        borderColor: "natural.medium", bgColor: "background.default"
                    }}>
                        <Typography p={2}>
                            {summary}
                        </Typography>
                    </Card>
                ) : (<Card sx={{
                    mt: 4, border: 1, boxShadow: 0, height: "500px", borderRadius: 5,
                    borderColor: "natural.medium", bgColor: "background.default"
                }}>
                    <Typography variant="h5" color="natural.main"
                        sx={{ textAlign: "center", verticalAlign: "middle", lineHeight: "450px" }}>
                        Summary will appear here
                    </Typography>
                </Card>)}</>) : (<><Typography variant="h5" color="natural.main" sx={{ mb: 2 }}>You must Sign in to access this feature:<NavLink to="/login" p={1}>Sign In</NavLink></Typography>
                    <Typography variant="h5" color="natural.main" sx={{ mb: 2, mt: 2 }}>If no account then register here:<NavLink to="/register" p={1}>Register</NavLink></Typography>

                </>
            )}

        </Box>
    );
}

export default Summary;